<template>
  <div class="col-6 mx-auto">
    <div class="card">
      <div class="card-header">
        Sign In or <router-link to="/signup">Signup</router-link>
      </div>
      <div class="card-body">
        <form method="post">
          <div class="form-row">
            <div class="input-group">
              {{ errorMessage }}
            </div>
          </div>
          <div class="form-group">
            <input type="text" v-model="username" class="form-control" placeholder="Username">
          </div>
          <div class="form-group">
            <input type="password" v-model="password" class="form-control" placeholder="Password">
          </div>
          <button type="button" class="btn btn-primary" @click="doLogin()">Sign In</button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from '../http'

export default {
  data () {
    return {
      username: '',
      password: '',
      errorMessage: ''
    }
  },
  props: ['referrer'],
  created () {
    if (localStorage.getItem('token')) {
      this.goToHome()
    }
  },
  methods: {
    doLogin () {
      let loginData = {
        username: this.username,
        password: this.password
      }
      axios.post('login', loginData)
        .then(response => {
          localStorage.setItem('token', response.data.token)
          this.goToHome()
        })
        .catch(error => {
          this.errorMessage = 'Invalid login. ' + error
        })
    },
    goToHome () {
      this.$router.push('/')
    }
  }
}
</script>

<style>

</style>



