<template>
  <div class="wrapper">
        Logging out...
  </div>
</template>

<script>

export default {
  created () {
    if (localStorage.getItem('token')) {
      localStorage.removeItem('token')
      this.$router.push('/login')
    } else {
      this.$router.push('/login')
    }
  }
}
</script>

<style>

</style>



