<template>
  <div>
    <div class="row d-flex justify-content-between">
      <div class="col-3"><h1>Categories</h1></div>
      <div class="col-2 text-right"><button class="btn btn-success" @click="doAdd()" data-toggle="modal" data-target="#categoryModal"><i class="fa fa-plus"> </i> <b>Add</b></button></div>
    </div>
    <table class="table table-hover">
    <thead>
      <tr>
        <th scope="col">#</th>
        <th scope="col">Category</th>
        <th scope="col">Edit</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(item, index) in categories" :key="index">
        <th scope="row">{{ index + 1 }}</th>
        <td>{{ item.title }}</td>
        <td><button href="#" class="btn btn-primary" @click="doEdit(item.id)" data-toggle="modal" data-target="#editModal"><i class="fa fa-edit"> </i> <b>Edit</b></button></td>
      </tr>
    </tbody>
  </table>
    <div class="modal fade" id="categoryModal" tabindex="-1" role="dialog" aria-labelledby="categoryModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Modal title</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
              <a href="#" @click="addPanel()" data-toggle="collapse" data-target="#collapseExample" aria-expanded="false" aria-controls="collapseExample">
                <i class="fa fa-plus"> Add</i>
              </a>
              <div class="collapse" id="collapseExample">
                <div class="form-group">
                  <label for="title">Title</label>
                  <input v-model="title" type="text" class="form-control" id="title" placeholder="Enter title">
                </div>
              </div>
              <div class="form-group" v-if="more === false">
                <label for="title">Title</label>
                <select class="form-control" v-model="title">
                  <option v-for="(item, index) in choices" :key="index">{{ item }}</option>
                </select>
              </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal" ref="closeBtn2" id="close">Close</button>
            <button type="button" class="btn btn-primary" @click="doSave()">Save changes</button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" id="editModal" tabindex="-1" role="dialog" aria-labelledby="editModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">{{ title }}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <form>
              <div class="form-group">
                <label for="title">Title</label>
                <input v-model="title" type="text" class="form-control" id="title" placeholder="Enter title">
              </div>
            </form>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal" ref="closeBtn" id="close">Close</button>
            <button type="button" class="btn btn-primary" @click="doPut()">Save changes</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from '../http'
export default {
  data () {
    return {
      more: false,
      add: false,
      choices: ['person', 'place', 'food'],
      id: '',
      title: '',
      errorMessage: '',
      categories: []
    }
  },
  created () {
    this.loadCategories()
  },
  methods: {
    loadCategories () {
      // use axios to get data
      axios.get('user-category')
        .then(response => {
          this.categories = response.data
        })
        .catch(error => {
          this.errorMessage = '' + error
        })
    },

    loadChoices () {
      var data = []
      for (var o in this.categories) {
        data.push(this.categories[o]['title'])
      }
      this.choices = this.choices.filter(f => !data.includes(f))
    },

    doEdit (id) {
      let data = this.categories.filter(f => {
        if (f['id'] === id) {
          return f
        }
      })[0]
      this.title = data['title']
      this.id = data['id']
    },

    doPut () {
      let data = {
        id: this.id,
        title: this.title
      }
      axios.put('user-category/' + data['id'] + '/', data)
        .then(response => {
          const elem = this.$refs.closeBtn
          if (response.status === 200) {
            this.categories = []
            this.loadCategories()
            elem.click()
          }
        })
        .catch(error => {
          this.errorMessage = error
        })
    },

    addPanel () {
      this.title = ''
      if (this.more === false) {
        this.more = true
        this.add = true
      } else {
        this.more = false
      }
    },

    doSave () {
      let data = {
        title: this.title
      }
      axios.post('user-category/', data)
        .then(response => {
          const elem = this.$refs.closeBtn2
          if (response.status === 201) {
            this.categories = []
            this.loadCategories()
            elem.click()
            this.add = false
          }
        })
        .catch(error => {
          this.errorMessage = error
        })
    },

    doAdd () {
      this.loadChoices()
    }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



