<template>
  <div class="wrapper">
    <div class="jumbotron jumbotron-fluid" style="padding: 2rem">
      <div class="container">
        <h3>Favorite Things <i class="fa fa-pencil"></i></h3>
        <a class="btn btn-primary btn-md" href="#" role="button" @click="doAdd()" data-toggle="modal" data-target="#favoriteModal" ><i class="fa fa-plus"> </i> Add Favourite</a>
      </div>
    </div>
    <div class="row">
      <div v-for="(item, index) in favoriteList" class="col-lg-4 card-row" :key="index">
        <div class="card" style="width: 18rem;">
          <div class="card-body">
            <h5 class="card-title">{{ item.title }}</h5>
            <h6 class="card-subtitle mb-2 text-muted"><i class="far fa-clock"></i> {{ item.dateCreated }}</h6>
            <hr>
            <p class="card-text">{{ item.description }}</p>
            <hr>
            <h6 class="card-subtitle mb-2 text-muted"><i class="far fa-folder-open"></i> {{ item.category }}</h6>
            <button href="#" class="btn btn-primary card-link" data-toggle="modal" data-target="#favoriteModal" @click="doEdit(item.id)">Edit</button>
          </div>
          <div class="card-footer text-muted">
            Ranking <i class="fa fa-sort-up"></i> {{ item.ranking }} Updated <i class="fa fa-clock-o"> {{ item.modified_date }}</i>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" id="favoriteModal" tabindex="-1" role="dialog" aria-labelledby="favoriteModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">{{ title }}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
              <div class="form-group">
                <label for="title">Title</label>
                <input v-model="title" type="text" class="form-control" id="title" placeholder="Enter title" required>
              </div>
              <div class="form-group">
                <label for="description">Description (Optional)</label>
                <textarea v-model="description" minlength="10" class="form-control" id="description" aria-describedby="descriptionHelp" placeholder="Enter title"></textarea>
                <small id="descriptionHelp" class="form-text text-muted">Not less than 10 characters</small>
              </div>
              <div class="form-group">
                <label for="title">Ranking</label>
                <input v-model="ranking" type="number" minlength="10" class="form-control" id="ranking" required>
              </div>
              <div class="form-group">
                <label for="title">Metadata</label>
                <input v-model="metadata" type="text" minlength="10" class="form-control" id="metadata">
              </div>
              <div class="form-group">
                <label for="category">Category</label>
                <div class="form-inline">
                  <select v-model="category" type="text" class="form-control" id="category" required>
                    <option :selected="item.id === category" v-for="(item, index) in categoryList" :key="index" :value="item.id">{{ item.title }}</option>
                  </select>
                  <a style="padding-left: 5px" href="/category"><i class="fa fa-plus"></i> Add</a>
                </div>
              </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal" ref="closeBtn" id="close">Close</button>
            <button type="button" class="btn btn-primary" @click="doSave()">Save changes</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from '../http'
export default {
  data () {
    return {
      favoriteList: [
      ],
      categoryList: [
      ],
      add: false,
      id: '',
      title: '',
      description: '',
      ranking: '',
      metadata: '',
      category: '',
      errorMessage: ''
    }
  },
  created () {
    this.loadFavoriteList()

    this.loadCategoryList()
  },
  methods: {
    loadFavoriteList () {
      axios.get('')
        .then(response => {
          let data = response.data
          data.map(data =>
            this.favoriteList.push({id: data.id, title: data.title, ranking: data.ranking, description: data.description, category: data.category_name, dateCreated: data.created_date, date_update: data.modified_date})
          )
        })
        .catch(error => {
          this.errorMessage = 'Invalid login. ' + error
        })
    },

    loadCategoryList () {
      axios.get('user-category')
        .then(response => {
          this.categoryList = response.data
        })
        .catch(error => {
          this.errorMessage = error
        })
    },

    doEdit (id) {
      this.clear()
      this.add = false
      let data = this.favoriteList.filter(f => {
        if (f['id'] === id) {
          return f
        }
      })[0]
      let categoryId = this.categoryList.filter(f => {
        if (f['title'] === data['category']) {
          return f
        }
      })[0]
      this.id = data['id']
      this.title = data['title']
      this.description = data['description']
      this.metadata = data['metadata']
      this.ranking = data['ranking']
      this.category = categoryId['id']
    },

    doSave () {
      let data = {
        id: this.id,
        title: this.title,
        description: this.description,
        category: this.category,
        metadata: this.metadata,
        ranking: this.ranking
      }
      if (this.add === false) {
        axios.put('/' + data['id'] + '/', data)
          .then(response => {
            const elem = this.$refs.closeBtn
            if (response.status === 200) {
              this.favoriteList = []
              this.loadFavoriteList()
              elem.click()
            }
          })
          .catch(error => {
            this.errorMessage = error
          })
      } else {
        if (data['title'] && data['ranking'] && data['category']) {
          delete data.id
          axios.post('', data)
            .then(response => {
              const elem = this.$refs.closeBtn
              if (response.status === 201) {
                elem.click()
                this.favoriteList = []
                this.loadFavoriteList()
              }
            })
            .catch(error => {
              this.errorMessage = error
            })
        }
      }
    },

    doAdd () {
      this.add = true
      this.clear()
    },

    clear () {
      this.id = ''
      this.title = ''
      this.description = ''
      this.ranking = ''
      this.category = ''
      this.metadata = ''
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.card-row {
  padding-bottom: 30px;
}
</style>



