<template>
  <div id="app">
      <nav class="navbar navbar-expand-lg navbar-dark bg-primary">
    <router-link class="navbar-brand" to="/" exact>Favorite Things</router-link>
    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>

    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav mr-auto">
        <li class="nav-item">
          <router-link to="/" class="nav-link" active-class="active" exact>Home</router-link>
        </li>
        <li class="nav-item">
          <router-link to="/category" class="nav-link" active-class="active" exact>Category</router-link>
        </li>
        <li class="nav-item">
          <router-link to="/activities" class="nav-link" active-class="active" exact>Activities</router-link>
        </li>
      </ul>
      <ul class="navbar-nav pull-right">
        <li class="nav-item" v-if="auth === false">
          <router-link to="/login" class="nav-link" active-class="active" exact>Login</router-link>
        </li>
        <li class="nav-item" v-if="auth === false">
          <router-link to="/signup" class="nav-link" active-class="active" exact>Sign up</router-link>
        </li>
        <li class="nav-item" v-if="auth">
          <router-link to="/logout" class="nav-link" active-class="active" exact>Logout</router-link>
        </li>
      </ul>
    </div>
  </nav>

    <div class="container" style="padding-top: 20px">
        <router-view/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      auth: false
    }
  },
  created () {
    this.authenticate()
  },
  watch: {
    $route (to, from) {
      this.authenticate()
    }
  },
  methods: {
    goToLogin () {
      this.$router.push('/login')
    },

    authenticate () {
      if (localStorage.getItem('token')) {
        this.auth = true
      } else {
        this.auth = false
        if (this.$router.currentRoute.name === 'SignUp') {
          this.$router.push('/signup')
        } else {
          this.$router.push('/login')
        }
      }
    }
  }
}
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
  }
  a.active{
    color: orangered;
  }
  .error {
    color: #990000;
  }
</style>



